import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './style.css';

class SummaryDesktop extends Component {
  render() {
    return (
      <div id="summary" className={this.props.class}>
        <div className="row highlight-desktop">
          <div className="col-lg-4 col-md-4 col-sm-4 left">
            <img src={this.props.taskIcon} />
            <p>{this.props.task1}</p>
            <p>{this.props.subtask1}</p>
            <p>{this.props.subtask2}</p>
            <p>{this.props.task2}</p>
          </div>
          <div className="col-lg-4 col-md-4 col-sm-4 left">
            <img src={this.props.skillIcon} />
            <p>{this.props.skill1}</p>
            <p>{this.props.skill2}</p>
          </div>
          <div className="col-lg-4 col-md-4 col-sm-4 right">
            <div className="row">
              <img src={this.props.durationIcon} />
              <p>{this.props.duration}</p>
            </div>
            <div className="row">
              <img src={this.props.teamIcon} />
              <p>{this.props.team}</p>
            </div>
          </div>
        </div>
        <div className="summary-desktop">
          <p>{this.props.txt1}</p>
          <p>{this.props.txt2}</p>
        </div>
        <div className="row tag-desktop">
          <div className="col-lg-3 col-md-3 col-sm-3">
            <h5 className={this.props.color}>{this.props.tag1}</h5>
          </div>
          <div className="col-lg-3 col-md-3 col-sm-3">
            <h5 className={this.props.color}>{this.props.tag2}</h5>
          </div>
        </div>
      </div>

    );
  }
}

export default SummaryDesktop;
