import React, { Component } from 'react';

import './style.css';

class DemoDesktop extends Component {
  render() {
    return (
      <div className="demo-desktop">
        <div className="desktop">
          <div className="demo-screen-desktop">
            <img src={this.props.img2} />
          </div>
        </div>
      </div>
    );
  }
}

export default DemoDesktop;
