import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import Header from '../components/Header';
import Nav from '../components/Nav';
import DemoMobile from '../components/DemoMobile';
import SummaryMobile from '../components/SummaryMobile';
import TakeawayHeader from '../components/TakeawayHeader';
import TakeawayMobile1 from '../components/TakeawayMobile1';
import TakeawayMobile2 from '../components/TakeawayMobile2';

import iphone from '../img/iphone.png';
import medalarm1 from '../img/medalarm1.jpg';
import medalarm2 from '../img/medalarm2.jpg';
import medalarm3 from '../img/medalarm3.jpg';
import medalarm4 from '../img/medalarm4.jpg';
import medalarm5 from '../img/medalarm5.jpg';
import medalarm6 from '../img/medalarm6.jpg';
import medalarm7 from '../img/medalarm7.jpg';
import medalarm8 from '../img/medalarm8.jpg';
import logo from '../img/logo-black.png';
import time from '../img/icon-time.png';
import team from '../img/icon-team.png';
import task from '../img/icon-task.png';
import tool from '../img/icon-tool.png';
import good from '../img/icon-good.png';
import neutral from '../img/icon-neutral.png';
import bad from '../img/icon-bad.png';
import idea from '../img/icon-idea.png';
import './style.css';

class MedAlarm extends Component {
  render() {
    return (
      <div style={{"backgroundColor":"#D5F4FF"}}>
      <div className="pg-project">
        <Header
          title="Med Alarm"
          class="title"
          href="https://marvelapp.com/4gbje43"
          link="View Demo"
        />
        <Nav bg="medalarm-side" class="drop-medalarm" />
        <DemoMobile
          img1={iphone}
          img2={medalarm3}
          parentClass="demo"
          childClass1="iphone"
          childClass2="img_demo"
        />

        <SummaryMobile
          txt1="Med Alarm is a medicine-tracking app designed exclusively for elderly people."
          txt2="Finding out the problem that many elderly people have the difficulty
          with their medicine, we designed this app to help them keep track of their medicine."
          tag1="Elderly-friendly"
          tag2="Simple"
          color="tag-medalarm"
          durationIcon={time}
          duration="10 weeks"
          teamIcon={team}
          team="5 people"
          taskIcon={task}
          task1="UX designer"
          subtask1=" - User testing"
          subtask2=" - Propotyping"
          skillIcon={tool}
          skill="Marvel App"
          class="medalarm-bg"
          value="takeaways"
          handleClick={this.handleClick}
        />

        <div id="takeaways" style={{"backgroundColor":"#D5F4FF"}}>
          <TakeawayHeader
            title="1. Limit the use of color"
            subtitle=" -- The progress of color scheme decision"
            intro="The main part of home page is the medicine schedule of the day. Users can easily know the time, the
                  type and the amount of medicine they should take. The menu bar is at the bottom of the page for easier
                  access. Available options are: view medicine detail, view appointment detail, view contact list and manage
                  account."
          />
          <TakeawayMobile1
            img1={medalarm1}
            img2={medalarm2}
            img3={medalarm3}
            img_header1="1st iteration"
            img_header2="2nd iteration"
            img_header3="3rd iteration"
            bad={bad}
            neutral={neutral}
            good={good}
          />
          <TakeawayMobile2
            txt1={<p>This app is targeted at elderly people. This is a special user group who is not familiar with technology.
                  Moreover, this groups of users tend to have poor eyesight and poor finger touch on the screen, which are all
                  problems we need to take into consideration.
                  <br/><ins>Our idea</ins>: <b>1. Use different colors instead of texts to differentiate sections (different time period
                  of the day).</b><b> 2. Use large fonts.</b></p>}
            txt2={<p>The different colors don't help much in disinguishing different sections. It makes our app looks messy
                  and childish. Even worse, different background color makes texts harder to read, which is the last thing
                  we wanted to do in an app for the elders.
                  <br/><ins>Lesson</ins>: <b>Limit the use of colors</b></p>}
            txt3={<p>In the next iteration, we decided that we will only use 2 main colors throughout the apps. For distinguishing sections,
                  we finally made some trade-offs and used labels to separate different periods of the day.
                  <br/><ins>Our idea</ins>: <b>Use spacing and labels to divide sections.</b></p>}
            txt4={<p>However, one of the teammates strongly disagreed with the new color scheme. She really didn't like the green and
                  preferred the original dominant color.
                  <br/><ins>Lesson</ins>: <b>1. We as a team should communicate effectively when making every decisions.</b><b> 2. Raise
                  disagreement early so that we won't waste time in revising prototypes repeatedly</b></p>}
            txt5={<p>We had a dissension regarding color scheme. Some members wanted to stay with the current color scheme while others were
                  strongly against the current color scheme. We decided to find some online research regarding elderly people vision.
                  According to the research, soft pastels are easier to process for the elders. In the suggest color section of the research,
                  we found one color that is very similar to our original dominant color.
                  <br/><ins>Our ideas</ins>: <b>Getting inspiration from online user research</b></p>}
            txt6={<p>We finally reached on agreement for the color scheme. Everyone made great efforts and was proud of our final prototype.
                  It was a great experience of working in team and we successfully managed our dissonance.
                  <br/><ins>Lesson</ins>: <b>When there is a strong disagreement among members, it's better to turn to more professional
                  resources such as online user research.</b></p>}
            bad={bad}
            neutral={neutral}
            good={good}
            idea={idea}
          />
          <TakeawayHeader
            title="Low-fidelity Prototypes"
          />
        <div className="prototype">
          <div className="row">
            <div className="col-lg-4 col-md-4 col-sm-4">
              <img src={medalarm4} />
            </div>
            <div className="col-lg-4 col-md-4 col-sm-4">
              <img src={medalarm5} />
            </div>
            <div className="col-lg-4 col-md-4 col-sm-4">
              <img src={medalarm6} />
            </div>
          </div>
          <div className="row">
            <div className="col-lg-4 col-md-4 col-sm-4">
              <img src={medalarm7} />
            </div>
            <div className="col-lg-4 col-md-4 col-sm-4">
              <img src={medalarm8} />
            </div>
            <div className="col-lg-4 col-md-4 col-sm-4" />
          </div>
        </div>
        </div>
      </div>
      </div>
    );
  }
}

export default MedAlarm;
