import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Link } from 'react-router-dom';
import './style.css';

class SummaryMobile extends Component {
  constructor(props) {
    super(props);
    this.summary = React.createRef();
  }

  componentDidMount() {
    const target = ReactDOM.findDOMNode(this.summary.current);
    window.addEventListener('scroll', this.handleScroll);
    console.log("ref");
    console.log(document.getElementById('summary'));
  }

  handleScroll = e => {
    const bottom = e.target.body.scrollHeight - e.target.body.scrollTop === e.target.body.clientHeight;
    console.log("bottom");
    console.log(e.target.body.scrollTop);
    console.log(bottom);
  }

  render() {
    return (
      <div id="summary" onScroll={this.handleScroll} ref={this.summary} className={this.props.class}>
        <div className="summary-mobile">
          <p>{this.props.txt1}</p>
          <p>{this.props.txt2}</p>
        </div>
        <div className="row tag">
          <div className="col-lg-3 col-md-3 col-sm-3">
            <h5 className={this.props.color}>{this.props.tag1}</h5>
          </div>
          <div className="col-lg-3 col-md-3 col-sm-3">
            <h5 className={this.props.color}>{this.props.tag2}</h5>
          </div>
        </div>
        <div className="row highlight-mobile">
          <div className="col-lg-4 col-md-4 col-sm-4 left">
            <img src={this.props.taskIcon} />
            <p>{this.props.task1}</p>
            <p>{this.props.subtask1}</p>
            <p>{this.props.subtask2}</p>
            <p>{this.props.task2}</p>
          </div>
          <div className="col-lg-4 col-md-4 col-sm-4 left">
            <img src={this.props.skillIcon} />
            <p>{this.props.skill}</p>
          </div>
          <div className="col-lg-4 col-md-4 col-sm-4 right">
            <div className="row">
              <img src={this.props.durationIcon} />
              <p>{this.props.duration}</p>
            </div>
            <div className="row">
              <img src={this.props.teamIcon} />
              <p>{this.props.team}</p>
            </div>
          </div>
        </div>
      </div>

    );
  }
}

export default SummaryMobile;
