import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './style.css';

class Nav extends Component {
  render() {
    return (
      <div className="nav">
        <ul id={this.props.bg}>
          <li><Link to="/contact"><p>Contact</p></Link></li>
          <li><Link to="/about"><p>About</p></Link></li>
          <li>
            <div className="dropdown">
              <button className="dropbtn">Projects</button>
              <div className="dropdown-content" id={this.props.class}>
                <Link to="/lito_nail">Lito Nail</Link>
                <Link to="/capitalsphere">Capitalsphere</Link>
                <Link to="/med_alarm">Med Alarm</Link>
                <Link to="/family_hub">Family Hub</Link>
              </div>
            </div>
          </li>
        </ul>
      </div>
    );
  }
}

export default Nav;
