import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Projects from '../components/Projects.js';
import ProjectGuide from '../components/ProjectGuide.js';
import Header from '../components/Header.js';

import medAlarm from '../img/med-alarm-full.png';
import familyHub from '../img/family-hub-full.png';
import capitalsphere from '../img/capitalsphere-full.png';
import litoNail from '../img/lito-nail-full.png';
import logo from '../img/logo-black.png';
import './style.css';

class Home extends Component {
  constructor() {
    super();
    this.state={
      hover: ""
    }
  }

  handleHoverEnter = e => {
    console.log("this.state");
    console.log(e.target);
    this.setState({hover: e.currentTarget.id});
    console.log(this.state);
  }

  handleHoverLeave = e => {
    this.setState({hover: ""});
    console.log("left");
  }

  handleClick = e => {
    const target = document.getElementById("takeaways");
    console.log("target");
    console.log(e.target.title);
    target.scrollIntoView();
  }

  render() {
    return (
      <div className="home">
        <Header title="Hi! I'm Yuhan Wang (Hannah)" class="title" handleHoverEnter={this.handleHoverEnter} />
        <div className="project-guide">
          <ProjectGuide
            hover={this.state.hover}
            handleClick={this.handleClick}
          />
        </div>
        {/**************************Projects*************************/}
        <div className="row">
          <div className="logos">
          <div className="col-lg-3 col-md-3 col-sm-3" style={{"padding":"0"}}>
            <Projects
              id="litoNail"
              image={litoNail}
              className="litoNail"
              hover={this.state.hover}
              handleHoverEnter={this.handleHoverEnter}
              handleHoverLeave={this.handleHoverLeave}
              link="lito_nail"
            />
          </div>
          <div className="col-lg-3 col-md-3 col-sm-3" style={{"padding":"0"}}>
            <Projects
              id="capitalsphere"
              image={capitalsphere}
              className="capitalsphere"
              hover={this.state.hover}
              handleHoverEnter={this.handleHoverEnter}
              handleHoverLeave={this.handleHoverLeave}
              link="capitalsphere"
            />
          </div>
          <div className="col-lg-3 col-md-3 col-sm-3" style={{"padding":"0"}}>
            <Projects
              id="medAlarm"
              image={medAlarm}
              className="medAlarm"
              hover={this.state.hover}
              handleHoverEnter={this.handleHoverEnter}
              handleHoverLeave={this.handleHoverLeave}
              link="med_alarm"
            />
          </div>
          <div className="col-lg-3 col-md-3 col-sm-3" style={{"padding":"0"}}>
            <Projects
              id="familyHub"
              image={familyHub}
              className="familyHub"
              hover={this.state.hover}
              handleHoverEnter={this.handleHoverEnter}
              handleHoverLeave={this.handleHoverLeave}
              link="family_hub"
            />
          </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
