import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './style.css';

class Projects extends Component {
  render() {
    let projects;
    if (this.props.hover === "" || this.props.hover !== this.props.id || this.props.hover === "header") {
      projects = <div className="projects"
        id={this.props.id}
        onMouseEnter={this.props.handleHoverEnter}
      >
        <img src={this.props.image} alt={this.props.image} className="project" />
      </div>
    }
    else if (this.props.hover === this.props.id) {
      projects = <div className="projects"
        id={this.props.id}
        onMouseEnter={this.props.handleHoverEnter}
        onMouseLeave={this.props.handleHoverLeave}
        >
        <Link to={`/${this.props.link}`}>
          <img src={this.props.image} className="project-selected" />
        </Link>
      </div>
    }

    return (
      <div>
        {projects}
        {/**<h1>Simple is not everything</h1>
        <h1>When things get stuck, think reversely</h1>**/}
      </div>
    );
  }
}

export default Projects;
