import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import logo from '../img/logo-black.png';
import './style.css';

class Logo extends Component {
  render() {
    return (
      <div id="header" onMouseEnter={this.props.handleHoverEnter}>
        <Link to='/'>
          <div>
            <img src={logo} alt="logo" />
          </div>
        </Link>
        <p className={this.props.class}>{this.props.title}</p>
        <a className="demo-link" href={this.props.href}><p>{this.props.link}</p></a>
      </div>
    );
  }
}

export default Logo;
