import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './style.css';

class TakeawayMobile2 extends Component {
  render() {
    return (
      <div className="text">
        <div className="row">
          <div className="col-lg-1 col-md-1 col-sm-1" />
          <div className="col-lg-8 col-md-8 col-sm-8">
            <img src={this.props.idea} className="progress-icon-text"/>
            {this.props.txt1}
            <img src={this.props.bad} className="progress-icon-text"/>
            {this.props.txt2}
          </div>
          <div className="col-lg-3 col-md-3 col-sm-3" />
        </div>

        <div className="row">
          <div className="col-lg-3 col-md-3 col-sm-3" />
          <div className="col-lg-8 col-md-8 col-sm-8">
            <img src={this.props.idea} className="progress-icon-text"/>
            {this.props.txt3}
            <img src={this.props.neutral} className="progress-icon-text"/>
            {this.props.txt4}
          </div>
          <div className="col-lg-1 col-md-1 col-sm-1" />
        </div>

        <div className="row">
          <div className="col-lg-1 col-md-1 col-sm-1" />
          <div className="col-lg-8 col-md-8 col-sm-8">
            <img src={this.props.idea} className="progress-icon-text"/>
            {this.props.txt5}
            <img src={this.props.good} className="progress-icon-text"/>
            {this.props.txt6}
          </div>
          <div className="col-lg-3 col-md-3 col-sm-3" />
        </div>

      </div>
    );
  }
}

export default TakeawayMobile2;
