import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './style.css';

class TakeawayMobile1 extends Component {
  render() {
    return (
      <div className="row takeaway-mobile">
        <div className="col-lg-4 col-md-4 col-sm-4">
          <img src={this.props.bad} className="progress-icon"/>
          <h6>{this.props.img_header1}</h6>
          <img src={this.props.img1} />
        </div>
        <div className="col-lg-4 col-md-4 col-sm-4">
          <img src={this.props.neutral} className="progress-icon" />
          <h6>{this.props.img_header2}</h6>
          <img src={this.props.img2} />
        </div>
        <div className="col-lg-4 col-md-4 col-sm-4">
          <img src={this.props.good} className="progress-icon" />
          <h6>{this.props.img_header3}</h6>
          <img src={this.props.img3} />
        </div>
      </div>
    );
  }
}

export default TakeawayMobile1;
