import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './style.css';

class TakeawayDesktop1 extends Component {
  render() {
    return (
      <div className="row takeaway-desktop">
        <div className="col-lg-6 col-md-6 col-sm-6">
          <img src={this.props.bad} className="progress-icon-desktop"/>
          <h6>{this.props.img_header1}</h6>
          <div>
            <img src={this.props.img1} />
          </div>
        </div>
        <div className="col-lg-6 col-md-6 col-sm-6">
          <img src={this.props.good} className="progress-icon-desktop"/>
          <h6>{this.props.img_header2}</h6>
          <div>
            <img src={this.props.img2} />
          </div>
        </div>
      </div>
    );
  }
}

export default TakeawayDesktop1;
