import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import Header from '../components/Header';
import Nav from '../components/Nav';
import DemoMobile from '../components/DemoMobile';
import SummaryMobile from '../components/SummaryMobile';
import TakeawayHeader from '../components/TakeawayHeader';
import TakeawayMobile1 from '../components/TakeawayMobile1';
import TakeawayMobile2 from '../components/TakeawayMobile2';

import iphone from '../img/iphone.png';
import lito_demo from '../img/lito-demo.png';
import time from '../img/icon-time.png';
import team from '../img/icon-team.png';
import task from '../img/icon-task.png';
import tool from '../img/icon-tool.png';

import lito1 from '../img/lito1.png';
import lito2 from '../img/lito2.png';
import lito3 from '../img/lito3.png';
import lito4 from '../img/lito4.png';
import lito5 from '../img/lito5.png';
import lito6 from '../img/litonail-low.jpg';
import good from '../img/icon-good.png';
import neutral from '../img/icon-neutral.png';
import bad from '../img/icon-bad.png';
import idea from '../img/icon-idea.png';
import './style.css';

class LitoNail extends Component {


  handleClick = e => {
    const target = document.getElementById(e.currentTarget.value);
    console.log("takeawayse");
    console.log(target);
    target.scrollIntoView();
  }

  render() {
    return (
      <div style={{"backgroundColor":"#E6F4F1"}}>
      <div className="pg-project">
        <Header
          title="LITO NAIL and EYELASHES"
          class="title"
          href="https://xd.adobe.com/view/a428ae61-4d7c-44dc-51f1-be86213f3d36-d219/?fullscreen"
          link="View Demo"
        />
        <Nav bg="litonail-side" class="drop-litonail" />
        <DemoMobile img1={iphone} img2={lito_demo} />
        <SummaryMobile
          txt1="This is a website designed for a salon called Lito Nail and Eyelashes.
          Lito Nail is a Japanese style nail salon that provides manicure, eyelash extension
          and waxing services."
          txt2="The main purpose of its website is to show off the salon's uniqueness and
          attract more customers."
          tag1="Simple"
          tag2="Elegant"
          color="tag-litonail"
          durationIcon={time}
          duration="10 weeks"
          teamIcon={team}
          team="5 people"
          taskIcon={task}
          task1="UX designer"
          subtask1=" - User testing"
          subtask2=" - Propotyping"
          skillIcon={tool}
          skill="Adobe XD"
          class="litonail-bg"
          value="takeaways"
          handleClick={this.handleClick}
        />
        <div id="takeaways" style={{"backgroundColor":"#E6F4F1"}}>
          <TakeawayHeader
            title="Takeaways"
            subtitle=" -- Simple doesn't only mean less content"
          />
          <TakeawayMobile1
            img1={lito1}
            img2={lito2}
            img3={lito3}
            img_header1="1st iteration"
            img_header2="2nd iteration"
            img_header3="3rd iteration"
            bad={bad}
            neutral={neutral}
            good={good}
          />
          <TakeawayMobile2
            txt1={<p>Upon request of our client, our website should be <i>simple</i> and <i>elegant</i>.
                  <br/><ins>Our idea</ins>: <b>Omit everything 'unnecessary' to create a minimalist website</b>.</p>}
            txt2={<p>After doing user testing, we found that on homepage, users were unable to find important info
                  about the salon, they even couldn't know the salon's name (which we thought is informed by the logo)
                  <br/><ins>Lesson</ins>: <b>1. Make careful decision about what can omit and what cannot. 2. Always remind ourselves that
                  users know nothing about this salon.</b></p>}
            txt3={<p>This time we put salon name and its feature in the most outstanding place. Moreover, we put a short intro
                  to better introduce the uniqueness of this salon to attract customers.
                  <br/><ins>Our idea</ins>: <b>Provide detailed intro on homepage for those who are interested in this salon.</b></p>}
            txt4={<p>When tesing on users, we found that they spent longer time on homepage than the 1st iteration because of
                  reading the paragraph. They admitted that in real life they will not spend time reading it.
                  <br/><ins>Lesson</ins>: <b>No one will read a paragraph especially on homepage.</b></p>}
            txt5={<p>We reconsidered the key contents we need to display: salon name, key feature, uniqueness, services.
                  To avoid too much contents, we realized that we need to make use of background image and icons which we
                  ignored before. Moreover, when reread the short intro, we found that there are lots of words waiting to be deleted.
                  <br/><ins>Our idea</ins>: <b> 1. Display contents in form of short phrases. 2. Use symbolic icon to increase readability.
                  3. Efficiently use background image to evoke feelings.</b></p>}
            txt6={<p>The last iteration works well! Users really love the background image and they can know all basic info about
                  the salon all in one view without the need to scroll down. Our professor loves the symbolic icons a lot. He thinks
                  that it iw a smart way to display the services; it also avoids unappealing images such as waxing.
                  <br/><ins>Lesson</ins>: <b>List all the contents that must go on the page. Make use of background, icons to substitute
                  texts. When texts are unavoidable, use short phrases.</b></p>}
            bad={bad}
            neutral={neutral}
            good={good}
            idea={idea}
          />
          <TakeawayHeader
            title="Low-fidelity sketch"
          />
          <img src={lito6} />
        {/**<TakeawayHeader
          title="2. There are always trade-offs - Detailedness or Shorter page?"
          subtitle=" -- The progress of Menu page"
        />
          <TakeawayMobile1
            img2={lito4}
            img3={lito5}
            img_header2="2nd iteration"
            img_header3="3rd iteration"
            neutral={neutral}
            good={good}
            className="col-lg-4"
          />
          <TakeawayMobile2
            txt1={<p>When first receiving the menu of Lito Nail, we can only understand it with the help of our
                  client. The menu contains many vague terms that only make sense to those who know manicure well.
                  We determined to
                  <br/><ins>Our idea</ins>: <b>1. Use plain English. 2. Use images to help understanding</b>.</p>}
            txt2={<p>Based on user testing feedback, users find it hard to
                  <br/><ins>Lesson</ins>: <b>1. It's better to limit menu to one page long so that users can know
                  all the services without scrolling. 2. Images are powerful when they correspond to texts, otherwise
                  it can be really distracted.</b></p>}
            txt3={<p>
                  <br/><ins>Our idea</ins>: <b>Provide detailed intro on homepage for those who are interested in this salon.</b></p>}
            txt4={<p>When tesing on users, we found that they spent longer time on homepage than the 1st iteration because of
                  reading the paragraph. They admitted that in real life they will not spend time reading it.
                  <br/><ins>Lesson</ins>: <b>No one will read a paragraph especially on homepage.</b></p>}
            txt5={<p>We reconsidered the key contents we need to display: salon name, key feature, uniqueness, services.
                  To avoid too much contents, we realized that we need to make use of background image and icons which we
                  ignored before. Moreover, when reread the short intro, we found that there are lots of words waiting to be deleted.
                  <br/><ins>Our idea</ins>: <b> 1. Display contents in form of short phrases. 2. Use symbolic icon to increase readability.
                  3. Efficiently use background image to evoke feelings.</b></p>}
            txt6={<p>The last iteration works well! Users really love the background image and they can know all basic info about
                  the salon all in one view without the need to scroll down. Our professor loves the symbolic icons a lot. He thinks
                  that it iw a smart way to display the services; it also avoids unappealing images such as waxing.
                  <br/><ins>Lesson</ins>: <b>List all the contents that must go on the page. Make use of background, icons to substitute
                  texts. When texts are unavoidable, use short phrases.</b></p>}
            bad={bad}
            neutral={neutral}
            good={good}
            idea={idea}
          />**/}
        </div>
      </div>
      </div>
    );
  }
}

export default LitoNail;
