import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Header from '../components/Header';
import logo from '../img/logo-black.png';
import phone from '../img/icon-phone.png';
import email from '../img/icon-email.png';

import './contact.css';

class Contact extends Component {
  render() {
    return (
      <div className="pg-contact">

        <Header />

        <p className="title-contact" style={{"color":"#FFF7E9"}}>
          <span>Currently looking for position as a</span>
          <br />
          UX designer
          <span style={{"padding":"0 0.8em"}}>or</span>
          Web developer
        </p>

        <div className="row contact">
          <div className="col-lg-6 col-md-6 col-sm-6">
            <img src={phone} alt="phone" />
            <p>(858) 405 - 6746</p>
          </div>
          <div className="col-lg-6 col-md-6 col-sm-6">
            <img src={email} alt="email" />
            <p>yuw431@gmail.com</p>
          </div>
        </div>

        <p className="thanks">
          Thank you :)
        </p>
      </div>
    );
  }
}

export default Contact;
