import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './style.css';

class ProjectGuide extends Component {
  render() {
    let guide;
    if (this.props.hover === "" || this.props.hover === "header") {
      guide = <div className="default">
        <p>UX Designer | Web Developer</p>
        <div className="learn-more">
          <Link to='/about'>
            <button>
              About me
            </button>
          </Link>
          <Link to='/contact'>
            <button style={{"marginLeft":"10%"}}>
              Contact
            </button>
          </Link>
        </div>
      </div>
    }
    else if (this.props.hover === "capitalsphere") {
      guide = <div className="guide">
        <p>I'm learning a lot from the projects</p>
        <Link to='/capitalsphere'>
          <p className="links">The hard decisions behind the simple page</p>
        </Link>
        <Link to='/capitalsphere'>
          <p className="links">The hard decisions behind the simple page</p>
        </Link>
      </div>
    }

    else if (this.props.hover === "medAlarm") {
      guide = <div className="guide">
        <p>I'm learning a lot from the projects</p>
        <Link to='/med_alarm'>
          <p></p>
        </Link>
      </div>
    }

    else if (this.props.hover === "litoNail") {
      guide = <div className="guide">
        <p>I'm learning a lot from the projects</p>
        <Link to='/lito_nail'>
          <p className="links" title="takeaways">The hard decisions behind the simple page</p>
        </Link>
      </div>
    }

    else if (this.props.hover === "familyHub") {
      guide = <div className="guide">
        <p>I'm learning a lot from the projects</p>
        <Link to='/family_hub'>
          <p></p>
        </Link>
      </div>
    }
    return (
      <div className="default">
        <p>UX Designer | Web Developer</p>
        <div className="learn-more">
          <Link to='/about'>
            <button>
              About
            </button>
          </Link>
          <Link to='/contact'>
            <button style={{"marginLeft":"10%"}}>
              Contact
            </button>
          </Link>
        </div>
      </div>
    );
  }
}

export default ProjectGuide;
