import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import Header from '../components/Header';
import Nav from '../components/Nav';

import logo from '../img/logo-black.png';
import me from '../img/me.jpg';
import hobby from '../img/icon-hobby.png';
import education from '../img/icon-education.png';
import progress from '../img/icon-progress.png';
import skill from '../img/icon-skill.png';
import goal from '../img/icon-goal.png';

import resume from '../img/Resume.pdf';
import './about.css';

class AboutMe extends Component {
  handleClick = e => {
    const target = document.getElementById(e.target.title);
    target.scrollIntoView();
  }
  render() {
    return (
      <div className="pg-about">
        <Header title="It's so nice to meet you!" class="title" />
        <Nav bg="about-side" class="drop-about" />
        <div className="shadow">
          <img src={me} alt="me" />
        </div>
        <div className="self-intro">
        <div className="row">
          <div className="col-lg-1">
            <img src={education} alt="education" />
          </div>
          <div className="col-lg-11">
            <p>
              University of California, San Diego (UCSD)
              <br />
              Cognitive Science w/Spec Human Computer Interaction
            </p>
          </div>
        </div>

        <div className="row">
          <div className="col-lg-1">
            <img src={skill} alt="skill" />
          </div>
          <div className="col-lg-11">
            <p>
              Storyboarding, Prototyping, User testing
              <br />
              Adobe XD
              <br />
              JavaScript, HTML, css, Reactjs, Java, WordPress
            </p>
          </div>
        </div>
        <div className="row">
          <div className="col-lg-1">
            <img src={goal} alt="goal" />
          </div>
          <div className="col-lg-11">
            <p>
              "If I don't understand one's behavior, it's not because she/he is
              wierd, but because I've never had his/her experience before."
            </p>
          </div>
        </div>
        </div>
        <div className="txt">
        <p>Hi there! I'm Yuhan. I love <i>coding</i> for its <b>logic</b> and <i>neuroscience</i>
          for its <b>focus on people</b>. I have a deep interest in understanding and helping people with what
          I learnt. Understanding people and helping them reach their goals is the career that
          I want to pursue for my whole lifetime.
          {/**What I am most proud of myself is that I reflect myself frequently, when I fail and when I succeed
          at something. It enables me to know myself clearly and improve myself consciously. I think that understanding
          myself clearly is important to understand others.**/}
        </p>
        </div>

        <div className="resume">
          <p className="title" style={{"color":"black", "paddingBottom":"40px"}}>Resume</p>
          <div id="resume">
            <embed src={resume} />
          </div>
        </div>
      </div>
    );
  }
}

export default AboutMe;
