import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import Header from '../components/Header';
import Nav from '../components/Nav';
import DemoDesktop from '../components/DemoDesktop';
import SummaryDesktop from '../components/SummaryDesktop';
import TakeawayHeader from '../components/TakeawayHeader';
import TakeawayDesktop1 from '../components/TakeawayDesktop1';
import TakeawayDesktop2 from '../components/TakeawayDesktop2';
import TakeawayMobile2 from '../components/TakeawayMobile2';

import iphone from '../img/iphone.png';
import familyhub_demo from '../img/familyhub_demo.jpg';
import familyhub1 from '../img/familyhub1.jpg';
import logo from '../img/logo-black.png';
import time from '../img/icon-time.png';
import team from '../img/icon-team.png';
import task from '../img/icon-task.png';
import tool from '../img/icon-tool.png';
import desktop from '../img/desktop1.png';
import good from '../img/icon-good.png';
import neutral from '../img/icon-neutral.png';
import bad from '../img/icon-bad.png';

import idea from '../img/icon-idea.png';
import './style.css';

class FamilyHub extends Component {

  handleClick = e => {
    console.log("takeawayse");
    console.log(e.target.scrollTop);
    const target = document.getElementById(e.currentTarget.value);
    target.scrollIntoView();
  }

  render() {
    return (
      <div style={{"backgroundColor":"#FCDC84"}}>
      <div className="pg-project">
        <Header
          title="Family Hub"
          class="title"
          href="https://alankuo.github.io/SleepMaster_FamilyHub/#/"
          link="View Website"
        />
        <Nav bg="familyhub-side" class="drop-familyhub" />
        <DemoDesktop
          img1={desktop}
          img2={familyhub_demo}
          parentClass="demo-d"
          childClass1="desktop"
          childClass2="img-d"
        />
        <SummaryDesktop
          txt1="FamilyHub is a web app that suggests various activities for families"
          txt2="We found that the relationship between family members is becoming less tightened.
          We hope our app can help increase family interaction."
          tag1="warm"
          color="tag-familyhub"
          durationIcon={time}
          duration="10 weeks"
          teamIcon={team}
          team="5 people"
          taskIcon={task}
          task1="UX designer"
          subtask1=" - User testing, Prototyping"
          task2="Web Developer"
          skillIcon={tool}
          skill1="React"
          skill2="Marvel App"
          class="familyhub-bg"
          value="takeaways"
          handleClick={this.handleClick}
        />
        <div id="takeaways" style={{"backgroundColor":"#FCDC84"}}>
          <TakeawayHeader
            title="Takeaway"
            subtitle=" -- Always remind ourselves of the key feature of our design"
            intro="On the homepage, a family photo is displayed in order to evoke a sense of warmness among
            users. User can also leave messages on the message board and all their family member can view it.
            Since the key feature of this web app is exploring activities for families to do together, two big
            buttons are displayed to increase their efficiency in using the app."
          />
          <TakeawayDesktop1
            img1={familyhub1}
            img2={familyhub_demo}
            img_header1="Home page - 1st iteration"
            img_header2="Home page - 2nd iteration"
            bad={bad}
            neutral={neutral}
            good={good}
          />
        <TakeawayDesktop2
           txt1={<p>Since our web app is designed for family which includes not only people who are familiar with technology,
                 but also people who are less familiar, we want to make our website simple and straightforward.
                 <br/><ins>Our idea:</ins><b>Only have 3 distinct sections on the homepage -- family photo, message board,
                 ad explore button which is our key feature</b></p>}
            txt2={<p>When conductin user testing, we found that users tend to stay too long on the message board and viewing
                  family photo. Clicking on the button is the last thing they would do because of the need to scroll down.
                  <br/><ins>Lesson:</ins><b>Never hide the most important features from the first view</b></p>}
            txt3={<p>We realized that our problem is that the key features of our web app (exploring activities) is not
                  obvious to users on homepage.
                  <br/><ins>Our solution:</ins><b>Controll all sections on same page, especially the buttons that we want
                  users to click on.</b></p>}
            txt4={<p>When doing user testing on the last iteration, we got this feedback that the button is really big and straightforward.
                  Compared with the last prototype, our key features are way more emphasized. Because of the restriction of time,
                  we did not have the time to further polish our prototype.
                  </p>}
            bad={bad}
            neutral={neutral}
            good={neutral}
            idea={idea}
          />
        </div>
      </div>
      </div>
    );
  }
}

export default FamilyHub;
