import React, { Component } from 'react';

import './style.css';

class DemoMobile extends Component {
  render() {
    return (
      <div className="demo-mobile">
        <div className="iphone">
          <div className="demo-screen">
            <img src={this.props.img2} />
          </div>
        </div>
      </div>      
    );
  }
}

export default DemoMobile;
